import React from 'react'
import ImageGallery from 'react-image-gallery'
import '../../../node_modules/react-image-gallery/styles/css/image-gallery-no-icon.css'
import { ImageWrapper } from './Carousel.style'
import { Link } from 'react-router-dom'
import { LoaderWrapper, Loader } from 'styles'

class Carousel extends React.Component {

    render() {
        const { events } = this.props.events;

        const images = events.length ? events.map(event => (
            {
                renderItem: () => (
                    <Link to={`evento/${event.id}`}>
                        <img src={`${event.image ? `images/${event.image}` : 'images/event-image-default.png'}`} alt="" />
                    </Link>
                ),
                description: event.name,
                originalAlt: '',
                onClick: id => this.openEventItem(event.id)
            }
        )) : [];

        return (
            <React.Fragment>
                {events.length ?
                    <ImageWrapper>
                        <ImageGallery items={images} autoPlay={true} showThumbnails={false} />
                    </ImageWrapper>
                    :
                    <LoaderWrapper>
                        <Loader size={25} color='orange' />
                    </LoaderWrapper>
                }
            </React.Fragment>
        );
    }
}

export { Carousel }
