import styled from 'styled-components'
import { Plus } from 'styled-icons/evil'
import { colors } from 'styles'

export const NewEventsItem = styled.div`
    background-color: ${colors.lightGray3};
    position: relative;
    height: 150px;
    box-shadow: 0 1px 5px rgba(0, 0, 0, 0.46);
    border-radius: 5px;
    @media(min-width: 768px) {
        height: 300px;
    }
`;

export const NewEventsItemImage = styled.img`
    height: 100%;
    width: 100%;
    border-radius: 5px;
    object-fit: cover;
    object-position: center;
`;

export const NewEventsItemName = styled.p`
    background-color: rgba(0, 0, 0, 0.5);
    left: 0;
    margin:0
    padding: 2px 10px;
    position: absolute;
    bottom: 20px;
    height: 20px;
    font-size: 12px;
    color: ${colors.white};
`;

export const NewEventsButton = styled.button`
    height: 100%;
    width: 100%;
    background-color: ${colors.orange};
    border: 2px solid ${colors.orange};
    border-radius: 5px;
`;

export const PlusIcon = styled(Plus)`
    color: ${colors.lightGray3};
`;