import React from 'react'
import { connect } from 'react-redux'
import { Container, Row, Title, SubTitle, Text, Button } from 'styles'
import { LocationIcon, TimeIcon, TextGroup, TextWrapper, DescriptionGroup, EventImage } from './Event.style'

class Event extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            event: {},
            eventAddress: {},
            eventLoading: false,
        };
    }

    componentDidMount() {

        const { events, match } = this.props;
        const event = events.length ? events.find(event => event.id === parseInt(match.params.id)) : {};

        this.setState({ event });
    }

    render() {
        const { event } = this.state;

        return (
            <React.Fragment>
                {Object.keys(event).length !== 0 ?
                    (<React.Fragment>
                        <EventImage src={`/images/${event.image}`}></EventImage>
                        <Container>
                            <Title>{event.name}</Title>
                            <TextGroup>
                                <TimeIcon size="15" />
                                <Text>{event.date} - {event.hour}</Text>
                            </TextGroup>
                            <TextGroup>
                                <LocationIcon size="15" />
                                <TextWrapper>
                                    <Text>{event.address.street}, {event.address.number}</Text>
                                    <Text>{event.address.neighborhood} - {event.address.city}/{event.address.uf}</Text>
                                </TextWrapper>
                            </TextGroup>
                            <DescriptionGroup>
                                <SubTitle>Descrição do Evento</SubTitle>
                                <Text>{event.description}</Text>
                            </DescriptionGroup>
                            <Row>
                                <Text size={25} style={{ alignSelf: 'center' }}>R$ {event.priceTicket.toLocaleString('pt-BR')}</Text>
                                <Button type="orange">Comprar</Button>
                            </Row>
                        </Container>
                    </React.Fragment>) : null}
            </React.Fragment>
        );
    }
}

const mapStateToProps = state => {
    return {
        events: state.events.events
    };
};

export default connect(
    mapStateToProps,
    null
)(Event);

