import styled from 'styled-components'
import { Location } from 'styled-icons/octicons'
import { Time } from 'styled-icons/boxicons-regular'

export const TextGroup = styled.div`
    display: flex;
    justify-content: start;
    margin-bottom: 10px;
    p {
        margin: 0 10px 0;
        line-height: 12px;
    }
`;

export const TextWrapper = styled.div`
`;

export const DescriptionGroup = styled.div`
    margin-bottom: 10px;

    p {
        line-height: 12px;
    }
`;

//Icons
export const LocationIcon = styled(Location)``
export const TimeIcon = styled(Time)``

export const EventImage = styled.img`
    ${({ imageSrc }) => `background-image: url("images/${imageSrc}")`};
    max-height: 300px;
    width: 100%;
    object-fit: cover;
    object-position: center;
    margin: 0;

    @media(max-width: 768px) {
        max-height: 200px;
    }
`