import React from 'react'
import { connect } from 'react-redux'
import { Container, Title, Button, Wrapper } from 'styles'
import { Carousel, NewEvents } from 'components/Home'
import { Header } from 'components/Shared'

class Home extends React.Component {

    componentDidMount() {

    }

    handlePublishEvents = () => {
        console.log('teste')
    }

    render() {
        const { events } = this.props;

        return (
            <React.Fragment>
                <Container>
                    <Header />

                    <Title>Destaques</Title>
                    <Carousel events={events} />

                    <Title>Novos Eventos</Title>
                    <NewEvents events={events} />

                    <Wrapper justifyContent="center">
                        <Button onClick={this.handlePublishEvents}>Publicar Evento</Button>
                    </Wrapper>
                </Container>
            </React.Fragment>
        );
    }
}

const mapStateToProps = state => {
    return {
        events: state.events
    };
};

export default connect(
    mapStateToProps,
    null
)(Home);
