import styled, { keyframes } from 'styled-components'
import { colors } from 'styles'
import { LoaderAlt } from 'styled-icons/boxicons-regular'

// Page
export const Container = styled.div`
        padding: 10px 20px 20px 20px;
        width: 1140px;
        margin: 0 auto;
        
        @media(max-width: 1200px) {
                width: 960px;
        }

        @media(max-width: 992px) {
                width: 720px;
        }

        @media(max-width: 768px) {
                width: auto;
        }
`;

export const PageHeader = styled.div`
        flex-direction: row;
        align-items: center;
        padding: 15px 0;
`;

export const PageTitle = styled.h1`
        font-size: 22px;
        line-height: 5px;
        font-weight: 400;
        ${({ color }) => color ? `color: ${color};` : `color: ${colors.gray};`}
`;

export const PageSubTitle = styled.h3`
        font-size: 15px;
        ${({ color }) => color && `color: ${color};`}
`;

export const Title = styled.h3`
        color: ${colors.gray};
        font-size: 17px;
        font-weight: 600;
        margin: 0 0 10px;
`

export const SubTitle = styled.h4`
        color: ${colors.gray};
        font-size: 14px;
        font-weight: 600;
        margin: 0 0 10px;
`

export const Text = styled.p`
        bottom: 20px;
        height: 20px;
        ${({ color }) => color ? `color: ${color}` : `color: ${colors.gray}`}; 
        ${({ size }) => size ? `font-size: ${size}px` : `font-size: 12px`};
        ${({ bold }) => bold ? `font-weight: ${bold}` : `font-weight: 400`};  
        width: 100%;
        margin: 0;
`

export const Logo = styled.img`
        max-width: 200px;
`
export const LoaderWrapper = styled.div`
        display: flex;
        min-height: 150px;
        align-items: center;
        justify-content: center;
`

const rotate = keyframes`
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
`;

export const Loader = styled(LoaderAlt)`
        animation: ${rotate} 2s linear infinite;
`

// Form
export const Wrapper = styled.div`
        display: flex;
        ${({ justifyContent }) => justifyContent ? `justify-content: ${justifyContent}` : `justify-content: start`}; 
        margin: 20px 0;
`;

export const Button = styled.a`
        display: inline-block;
        border-radius: 3px;
        padding: 10px;
        ${({ type }) =>
                type === "orange" ?
                        `background-color: ${colors.orange};
            border: 2px solid ${colors.orange};
            color: ${ colors.white};`
                        :
                        `background-color: transparent;
            border: 2px solid ${colors.orange};
            color: ${ colors.orange};`
        }
`

// Row
export const Row = styled.div`
        display: flex;
        flex: 1;
        flex-direction: row;
        margin: 0 -5px;
        ${({ bottom }) => bottom ? `padding-bottom: ${bottom}` : `padding-bottom: 0`};
        
`;

export const Column = styled.div`
        ${({ flex }) => flex ? `flex: ${flex}` : `flex: 1`}; 
        margin: 0 5px;
`;