import React from 'react'
import { connect } from 'react-redux'
import { Logo, PageHeader, Row, Column } from 'styles'
import { SearchButton, SearchIcon } from './Header.style'
import Geocode from "react-geocode"
import 'react-notifications/lib/notifications.css'
import { NotificationContainer, NotificationManager } from 'react-notifications'
import { searchEventByCity } from 'redux/actions/events.actions'
import { history } from 'helpers'

class Header extends React.Component {

    onSearchEvents = city => {
        history.push('eventos');
    }

    getLocation = () => {
        if (navigator.geolocation) {
            navigator.geolocation.getCurrentPosition(this.showPosition, this.showError);
        } else {
        }
    }

    showPosition = position => {
        Geocode.setApiKey("AIzaSyCk9ypTPM8boIknGwDtBEnUJQcYcZ_lTWg");
        Geocode.fromLatLng(position.coords.latitude, position.coords.longitude).then(
            response => {
                const address = response.results[0];
                this.onSearchEvents(address.address_components[3].long_name);
            },
            error => {
                console.error(error);
            }
        );
    }

    showError = error => {
        if (error.code === 1) {
            NotificationManager.error('Ative sua localização para buscar um evento');
        }
    }

    render() {
        return (
            <PageHeader>
                <Row>
                    <Column flex={0.8}>
                        <Logo src="images/logoMarca.png" alt="" />
                    </Column>
                    <Column flex={0.2} style={{ textAlign: 'right' }}>
                        <SearchButton onClick={this.getLocation} aria-label="Botão para busca de eventos">
                            <SearchIcon size="20" />
                        </SearchButton>
                    </Column>
                </Row>
                <NotificationContainer />
            </PageHeader>
        );
    }
}

const mapStateToProps = state => {
    return {
        events: state.events
    };
};

const ConnectedHeader = connect(
    mapStateToProps,
    searchEventByCity
)(Header);

export { ConnectedHeader as Header }

