import React from 'react'
import { NewEventsItem, NewEventsItemImage, NewEventsItemName, NewEventsButton, PlusIcon } from './NewEvents.style'
import { Row, Column } from 'styles'
import { Link } from 'react-router-dom'
import { LoaderWrapper, Loader } from 'styles'

class NewEvents extends React.Component {

    render() {
        const { events } = this.props.events;

        return (
            <React.Fragment>
                {events.length ?
                    (<React.Fragment><Row bottom="10px">
                        <Column flex={0.5}>
                            <Link to={`evento/${events[0].id}`}>
                                <NewEventsItem>
                                    <NewEventsItemImage src={`images/${events[0].image}`} alt="" />
                                    <NewEventsItemName>{events[0].name}</NewEventsItemName>
                                </NewEventsItem>
                            </Link>
                        </Column>
                        <Column flex={0.5}>
                            <Link to={`evento/${events[1].id}`}>
                                <NewEventsItem>
                                    <NewEventsItemImage src={`images/${events[1].image}`} alt="" />
                                    <NewEventsItemName>{events[1].name}</NewEventsItemName>
                                </NewEventsItem>
                            </Link>
                        </Column>
                    </Row>
                        <Row>
                            <Column>
                                <Link to={`evento/${events[2].id}`}>
                                    <NewEventsItem>
                                        <NewEventsItemImage src={`images/${events[2].image}`} alt="" />
                                        <NewEventsItemName>{events[2].name}</NewEventsItemName>
                                    </NewEventsItem>
                                </Link>
                            </Column>
                            <Column>
                                <Link to="eventos">
                                    <NewEventsButton onClick={this.handleMoreEvent} aria-label="Botão para mostrar de eventos" ><PlusIcon size={50} /></NewEventsButton>
                                </Link>
                            </Column>
                        </Row>
                    </React.Fragment>)
                    :
                    <LoaderWrapper>
                        <Loader size={25} color='orange' />
                    </LoaderWrapper>
                }
            </React.Fragment>
        );
    }
}

export { NewEvents }
