import styled from 'styled-components'
import { colors } from 'styles'

export const EventItemWrapper = styled.div`
    background-color: ${colors.white};
    padding: 5px 0;
    margin-bottom: 5px;
`;

export const EventItemTextWrapper = styled.div`
    background-color: ${colors.white};
    padding-left: 10px;
`;

export const EventItemImage = styled.img`
    height: 100%;
    width: 100%;
    border-radius: 5px;
    object-fit: cover;
    object-position: center;

    @media(min-width: 768px) {
        height: 200px;
    }
`;