import styled from 'styled-components'

export const ImageWrapper = styled.div`
    border-radius: 5px;
    max-width: 100%;
    height: auto;
    margin-bottom: 20px;
        
    img {
        border-radius: 5px;
        box-shadow: 0 1px 5px rgba(0, 0, 0, 0.46);
        object-fit: cover;
        object-position: center;
    }
`